import {Component} from '@angular/core'
import {Feedback} from '../models/Feedback'
import {FeedbackService} from '../services/feedback.service'
import { MdButton } from '@angular2-material/button';
import {MD_INPUT_DIRECTIVES} from '@angular2-material/input/input'

@Component({
    selector: "feedbacks",
    template: `
<h2>Feedbacks</h2>
<div>
  <div style="float: left">
    <md-input placeholder="enter application name" #appname maxlength="100" class="demo-full-width">
    </md-input>
  </div>
  <button style="margin-left: 10px" md-raised-button (click) = "getFeedbacks(appname.value)">
  load
  </button>
</div>
<ul>
    <li *ngFor="#feedback of feedbacks">
        Title: {{feedback.title}}
        Text: {{feedback.text}}
    </li>
</ul>
`,
    providers: [FeedbackService],
    directives: [MdButton, MD_INPUT_DIRECTIVES]
})
export class FeedbacksComponent{

    private feedbacks: Feedback[];
    private application: string;

    constructor(private feedbackService: FeedbackService){
    }

    private getFeedbacks(appname: string){
        this.application = appname;
        if (this.application != ""){
            this.feedbackService
                .GetFeedbacks(this.application)
                .subscribe((data: Feedback[]) => this.feedbacks = data, error => console.log(error));
        }
    }

}
